

<template>
  <vue-img-cropper
    :cropInfo="cropInfo"
    :visible="jianQieTc">
  </vue-img-cropper>
</template>

<script>
	import VueImgCropper from './lib/imgJianQie/VueImgCropper'
	export default {
		name: 'app',
		data () {
			return {
				jianQieTc: true,
			  cropInfo: {
          img: "https://weijiweidaping-pro.oss-cn-qingdao.aliyuncs.com/" +
            "orgInfo/6fc6383483d0f0dec8e831190d0a3ede.jpeg"
          ,CropHeight: 90
          ,CropWidth: 90
        }
			}
		},
		components: {
			VueImgCropper
		}
	}
</script>


